const Leadership = () => {
  return (
    <div class="card shadow p-4">
      <h2 class="text-primary">Leadership and Commitment</h2>
      <p>
        Commitment from top management is a fundamental pillar of an established
        ISMS according to ISO/IEC 27001.
      </p>

      <h3 class="text-secondary">Responsibilities of Top Management</h3>
      <ul class="list-group mb-3">
        <li class="list-group-item">
          Demonstrating commitment to information security.
        </li>
        <li class="list-group-item">
          Ensuring alignment of the information security policy and objectives
          with the organization's strategic direction.
        </li>
        <li class="list-group-item">
          Providing necessary resources for effective ISMS implementation.
        </li>
        <li class="list-group-item">
          Communicating the importance of information security management and
          compliance with ISO/IEC 27001.
        </li>
        <li class="list-group-item">
          Encouraging leadership in information security within their areas of
          responsibility.
        </li>
        <li class="list-group-item">
          Promoting continual improvement of the ISMS.
        </li>
      </ul>

      <h3 class="text-secondary">Information Security Policy</h3>
      <p>
        A well-structured information security policy is the most visible way to
        demonstrate top managements commitment to information security.
      </p>
      <ul class="list-group mb-3">
        <li class="list-group-item">
          The policy must be appropriate for the organizations purpose.
        </li>
        <li class="list-group-item">
          It must include information security objectives or a framework for
          setting them.
        </li>
        <li class="list-group-item">
          It must demonstrate a commitment to compliance and continuous
          improvement.
        </li>
        <li class="list-group-item">
          The policy must be documented and communicated to all employees.
        </li>
        <li class="list-group-item">
          It should be made available to relevant interested parties as
          appropriate.
        </li>
      </ul>

      <h3 class="text-secondary">Roles, Responsibilities, and Authorities</h3>
      <p>
        Top management must ensure that responsibilities related to information
        security are clearly defined and communicated.
      </p>
      <ul class="list-group mb-3">
        <li class="list-group-item">
          Ensuring roles relevant to ISMS have clear responsibilities and
          authorities assigned.
        </li>
        <li class="list-group-item">
          Ensuring ISMS compliance with ISO/IEC 27001 requirements.
        </li>
        <li class="list-group-item">
          Reporting ISMS performance to top management.
        </li>
        <li class="list-group-item">
          Delegating responsibilities for reporting ISMS performance within the
          organization if necessary.
        </li>
      </ul>
    </div>
  );
};

export default Leadership;
